:root {
  --font-raleway: "Raleway", sans-serif;
  --font-poppins: "Poppins", sans-serif;
  --font-open-sans: "Open Sans", sans-serif;
}

:root {
  --button: hsl(360, 100%, 69%);
  --client: hsl(26, 96%, 56%);
  --promo: hsl(264, 100%, 50%);
  --white: hsl(0, 0%, 100%);
  --copyright: hsl(0, 0%, 79%);
  --light-text: hsl(0, 0%,55%);
  --button-shadow: hsl(0, 0%, 44%);
  --dark-text: hsl(0, 0%,20%);
  --comment: hsl(0, 0%, 13%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  color: var(--white);
  font-family: var(--font-raleway);
  min-height: 150vh;
}
body.noscroll {
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 3.125rem;
  margin-bottom: 1.125rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 4.375rem;
  }
}

h2 {
  font-size: 2.25rem;
  margin-bottom: 2rem;
}

h3 {
  font-size: 1.5625rem;
  margin-bottom: 1rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--white);
  font-size: 1.125rem;
}

.container {
  padding: 0 1.625rem;
  max-width: 73.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.promo {
  background-image: url("../images/background.png");
  background-color: var(--promo);
  background-repeat: no-repeat;
  background-position: 50% -75%;
  background-size: 150vh;
  height: 100vh;
}
@media (min-width: 40em) {
  .promo {
    background-position: 50% 100%;
    background-size: 250vh;
  }
}

button, .button {
  background-color: var(--button);
  box-shadow: 0 0.0625rem 0 var(--button-shadow);
  border-radius: 1.40625rem;
  padding-top: 0.5625rem;
  padding-right: 2rem;
  padding-bottom: 0.75rem;
  padding-left: 2rem;
}
@media (max-width: 71.8125em) {
  button, .button {
    display: none;
  }
}

.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1.5625rem;
  padding-bottom: 1.5625rem;
}
@media (min-width: 40em) {
  .header {
    padding-top: 3.125rem;
  }
}
@media (min-width: 71.875em) {
  .header__toggle {
    display: none;
  }
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.25rem;
  border-radius: 1.40625rem;
  background-color: var(--white);
  transition: all 200ms ease-in-out;
  transform-origin: 0.046875rem 0.109375rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header__menu {
  position: absolute;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translateX(-50%);
  background: var(--white);
  margin-top: 0.09375rem;
  padding: 1.625rem;
  border-radius: 1.40625rem;
}
.header__menu a {
  display: block;
  padding: 0.625rem;
  color: var(--dark-text);
  text-align: center;
}
@media (max-width: 71.8125em) {
  .header__links {
    display: none;
  }
}
.header__links a {
  font-size: 1.125rem;
  font-weight: 500;
}
.header__links a:not(:last-child) {
  margin-right: 3.875rem;
}